export default function Me() {
  const {
    session,
    services: { user, security },
  } = useServiceContextHook();

  const { t } = useTranslation();

  const [userData, setUserData] = useState(null);
  const [dataError, setDataError] = useState(null);
  const [confirmations, setConfirmation] = useState({
    login: { email: true, sms: false },
    transaction: { email: true, sms: false },
  });

  useEffect(() => {
    if (session?.user)
      (async () => {
        try {
          setDataError(null);
          if (!userData) {
            let { data, error } = await fetchUserData();
            if (error) throw new Error(error);
            setUserData(data);
          }
        } catch (error) {
          console.error(error);
          setDataError(error);
        }
      })();
    return user.abort;
  }, [session]);

  /**
   *
   * @param {Object} filter : ;
   * @returns
   */
  async function fetchUserData(
    filter = {
      include: ["orders", "security"],
    }
  ) {
    return await user.find(filter);
  }

  /**
   *
   * @param {String} type - Confirmation type - transaction, login
   */
  async function onConfirmationChange({ type, key, value }) {
    try {
      // send a request to update confirmation
      const newState = {
        ...confirmations,
        [type]: { ...confirmations[type], [key]: value },
      };
      let changes = {
        ...userData?.security?.confirmations,
        [type]: Object.keys(newState[type])
          ?.filter((k) => newState[type][k])
          .map((t) => String(t)?.toUpperCase()),
      };
      let { data, error } = await security.updateByID(userData?.security?.id, {
        confirmations: changes,
      });
      if (error) throw new Error(error.message);
      if (data) {
        notify("Done", "success");

        setConfirmation(newState);
      }
    } catch (err) {
      console.error(err);
      notify(err.message, "error");
    }
  }

  return dataError ? (
    // TODO: Add an Error Feedback here...
    <>Data Error</>
  ) : session?.user && userData ? (
    <>
      <div className="" style={{ paddingTop: 100 }}>
        {/* NOTIFICATION */}
        <div className="container p-0">
          <SettingCard title="Profile settings">
            <div className="setting_container">
              <ProfileSetting />
            </div>
          </SettingCard>

          <SettingCard title="Notification settings">
            <div
              className="setting_container wow animate__animated fadeInDown"
              data-wow-delay="0.4s"
            >
              <header>
                <h4 className="h4">{t("Notification Settings")}</h4>
              </header>
              <div className="setting_table_container">
                <SettingTable>
                  <thead>
                    <tr>
                      <th></th>
                      <th>{t("Email")}</th>
                      <th>{t("SMS")}</th>
                    </tr>
                  </thead>
                  <tbody>
                    {confirmations &&
                      Object.entries(confirmations).map(([type, tvalue]) => {
                        return (
                          <tr key={type}>
                            <td className="text-capitalize">
                              {t(String(type)?.replace(/_/gi, " "))}
                            </td>

                            {Object.entries(tvalue).map(([key, value]) => (
                              <td key={`${type}-${key}`}>
                                <Form.Check
                                  type="switch"
                                  id={`switch-${type}-${key}`}
                                  name={`${type}-${key}`}
                                  className="custom-switch-md "
                                  onChange={() => {
                                    onConfirmationChange({
                                      type,
                                      key,
                                      value: !value,
                                    });
                                  }}
                                  defaultValue={value}
                                  defaultChecked={userData?.security?.confirmations[
                                    type
                                  ].includes(String(key)?.toUpperCase())}
                                />
                              </td>
                            ))}
                          </tr>
                        );
                      })}
                  </tbody>
                </SettingTable>
              </div>
            </div>
          </SettingCard>

          <SettingCard title="Security setting">
            <div className="setting_container">
              <header>
                <h4 className="h4">{t("Security")}</h4>
              </header>
              <ul>
                <li className="password clear">
                  <ChangePasswordSetting />
                </li>
                <li className="recommended clear">
                  <GoogleAuthSetting />
                </li>
                <li className="phone clear">
                  <PhoneVerificationSetting />
                </li>
                <li className="email clear">
                  <MailVerificationSetting data={userData} />
                </li>
                <li className="verification clear">
                  <IDVerification />
                </li>
              </ul>
            </div>
          </SettingCard>

          <SettingCard title="Security setting">
            <div className="setting_container">
              <header>
                <h4 className="h4">{t("Payment methods")}</h4>
              </header>
              <PaymentMethodSetting
                userData={userData?.profile?.payment_methods}
              />
            </div>
          </SettingCard>
        </div>
      </div>
    </>
  ) : (
    <div style={{ minHeight: "50vh" }}>Must login</div>
  );
}
/**
 * @description modify user profile data
 * @returns
 */
function ProfileSetting() {
  const { t } = useTranslation();
  const [userData, setUserData] = useState(null);
  const {
    session,
    services: { user },
  } = useServiceContextHook();

  const { toggledPayload, onOpen, onClose, isOpen } = useToggler();

  /**
   * @description fetch profile data
   * @param {Object*} filter : ;
   */
  async function fetchUserData(filter = {}) {
    try {
      let { data, error } = await await user.find(filter);
      if (error) throw new Error(error);
      setUserData(data);
    } catch (error) {
      console.error(error);
    }
  }

  useEffect(() => {
    if (!userData) fetchUserData();
  }, []);

  function useFormRenderer(formData) {
    return ["Edit Profile", <KycForm.ModifyProfile {...{ formData }} />];
  }

  return (
    userData && (
      <div>
        <div className="d-flex justify-content-end">
          <button
            onClick={user?.logout}
            aria-label="logout"
            className="btn btn-danger"
            role="button"
          >
            {t("Logout")}
          </button>
        </div>

        <header>
          <h4 className="h4">{t("My profile")}</h4>
        </header>

        <div className="wow animate__animated fadeInDown" data-wow-delay="0.2s">
          <CardColumnBreaker>
            <UserInfoSection className="user_info">
              <span className="user_initial">
                {userData?.profile?.pname && userData?.profile?.pname[0]}
              </span>
              <p className="user_name">
                <div className="name">
                  <span>{userData?.profile?.pname}</span>
                  {userData?.verified && (
                    <span className="fas fa-check-circle verification-icon"></span>
                  )}
                </div>
                <small className="user_id">
                  <span>{userData?.id}</span>
                </small>
              </p>

              <button
                onClick={() => onOpen(userData?.profile)}
                className="edit_btn"
              >
                <i className="fas fa-edit"></i>
                {t("Edit")}
              </button>
            </UserInfoSection>

            <section>
              <ListGrid>
                {userData?.login_at && (
                  <li>
                    <p className="d-block">{t("Last login time :")}</p>{" "}
                    <span>
                      <Moment>{userData?.login_at}</Moment>
                    </span>
                  </li>
                )}
                {Array.isArray(session?.user?.security?.ip_address) &&
                  session?.user?.security?.ip_address?.length && (
                    <li>
                      <p className="d-block">IP :</p>
                      <span>
                        {session?.user?.security?.ip_address.join(", ")}
                      </span>
                    </li>
                  )}
              </ListGrid>
            </section>
            <hr />
            <section>
              <ListGrid spread>
                <li>
                  <p>Total orders</p>
                  <span className="pronounced">{userData?.total_orders}</span>
                </li>
                <li>
                  <p>Completion rate</p>
                  <span className="pronounced">
                    {userData?.total_orders &&
                      (userData?.total_completed_orders /
                        userData?.total_orders) *
                        100}
                    %
                  </span>
                </li>
                <li>
                  <p>
                    Your Reviews
                    <br />(<span className="text-success">Positive</span>/
                    <span className="text-danger">Negative</span>)
                  </p>
                  <span className="pronounced">
                    <span className="positive text-success">
                      {userData?.total_positive_reviews}
                    </span>
                    /
                    <span className="negative text-danger">
                      {userData?.total_negative_reviews}
                    </span>
                  </span>
                </li>
              </ListGrid>
            </section>
          </CardColumnBreaker>
        </div>

        <ModalForm
          {...{
            useFormRenderer,
            isOpen,
            onClose() {
              fetchUserData();
              onClose();
            },
            formData: toggledPayload,
          }}
        />
      </div>
    )
  );
}
/**
 * @description ID verification setting component
 * @returns
 */
function IDVerification() {
  const { t } = useTranslation();
  const {
    services: { kyc },
  } = useServiceContextHook();
  const { toggledPayload, onOpen, onClose, isOpen } = useToggler();
  const [data, setData] = useState(null);

  async function fetchData() {
    try {
      let { data, error, message } = await kyc.find({
        "filter[type]": "ID",
      });
      if (!data)
        throw new Error(
          error?.message || message || "Error fetching KYC.ID data"
        );
      setData(data?.result[0]);
    } catch (err) {
      console.error(err);
    }
  }

  function useFormRenderer(formData) {
    return ["ID Verification", <KycForm {...{ formData }} />];
  }

  useEffect(() => {
    fetchData();
  }, []);
  return (
    <SettingCardItem>
      <img
        className="setting_img"
        src={icon_verification}
        alt="Identity Verification"
      />
      <div className="setting_info">
        <p className="title">{t("Identity Verification")}</p>
        <span className="description">
          {t(
            "Identity Verification can protect your account security and increase transaction limits."
          )}
        </span>
      </div>

      <div className="setting_actions">
        {data && data?.status == "ACCEPTED" && (
          <>
            <span style={{ gap: 10, display: "flex", alignItems: "center" }}>
              <i className="fas fa-check-circle"></i>
              Submitted
            </span>
            <small className="text-muted">
              <Moment>{data?.createdAt}</Moment>
            </small>
          </>
        )}
        <button onClick={() => onOpen({})} className="btn btn_enable">
          {t("Enable")}
        </button>
      </div>
      <ModalForm
        {...{
          useFormRenderer,
          isOpen,
          onClose() {
            fetchData();
            onClose();
          },
          formData: toggledPayload,
        }}
      />
    </SettingCardItem>
  );
}

